import { Component, Input, OnInit, HostBinding, EventEmitter, Output, AfterViewInit, ElementRef} from '@angular/core';
import { QueryCondition } from '../../entity/querycondition';
import { SolutionService } from '../../service/solution.service';
import { NumberRangeValue, NumberRangeValueConvertor } from '../../entity/conditionvalue/numberrangevaue';
// import { NumberRangeValue } from '../../entity/conditionvalue/numberrangevaue';


@Component({
    selector: 'app-filter-number',
    templateUrl: './number.component.html'
})

export class NumberComponent implements OnInit,AfterViewInit{
   
    // @Input() item:any;
    @Input() autoLabel:boolean;
    @Input() disabled:boolean;
    @Output('searchHandle') searchHandle = new EventEmitter();

    /** */
    extend:boolean = false;
    /**label */
    showlabel:boolean = true;
    canNull:boolean = true;

    number:string;
    beginValue: any;
    endValue: any;
    precision: number;
    placeholder: string;
    beginPlaceHolder:string;
    endPlaceHolder:string;
    bigNumber:boolean;
    max:number;
    min:number;

    @HostBinding('class.filter-item-wrap')
    get isExtend(): boolean {
        return !this.extend && this.autoLabel;
    }
    @HostBinding('class.filter-item-hiddenlabel')
    get hidenLabel(): boolean {
        return !this.showlabel;
    }

    _item: any;
    numberRange: string;
    @Input() 
    set item(nr: any){
        this._item = nr;
        this.precision = nr.control.precision;
        this.bigNumber = nr.control.bigNumber;
        if(!this.bigNumber){
            this.min = -2147483648;
            this.max = 2147483647;
        }
        this.constructMultiFieldNumberRange(nr.value as NumberRangeValue);
        if(this._item && this._item.control && this._item.control.hasOwnProperty('isExtend')){
            this.extend = this._item.control.isExtend;
            if(!this.extend){
                this.showlabel = this._item.control.showLabel;
            }
        }
    }

    get item(){
        return this._item;
    }

    constructor(private solutionService:SolutionService,private readonly element: ElementRef){}

    ngOnInit(): void {
        if(this.item){
            this.beginPlaceHolder = this.item.beginPlaceHolder || '';
            this.endPlaceHolder = this.item.endPlaceHolder || '';
        }
    }

    ngAfterViewInit() {

    }

    getConditionItemTitle(field:QueryCondition){
        return this.solutionService.getConditionItemTitle(field);
    }


    constructMultiFieldNumberRange(value: NumberRangeValue) {
        this.beginValue = value.startValue == null ? null : value.startValue;
        this.endValue = value.endValue == null ? null : value.endValue;
      }

    numberChange(event){
        if(event){
            const dataArray = event.split("~");
            this._item.value.startValue = dataArray[0] ? dataArray[0] : null;
            this._item.value.endValue = dataArray[1] ? dataArray[1] : null;
        }
        else{
            this._item.value.startValue = null;
            this._item.value.endValue = null;
        }
        if (Object.keys(this._item.value).length === 0 || (this._item.value.startValue == null && this._item.value.endValue == null)) {
            this._item.valueText = ''
        } else if (this._item.value.startValue == null) {
            this._item.valueText = '~' + this._item.value.endValue;
        } else if (this._item.value.endValue == null) {
            this._item.valueText = this._item.value.startValue + '~';
        } else {
            this._item.valueText = this._item.value.startValue + '~' + this._item.value.endValue;
        }
        if(this.disabled){return;}
        this.searchHandle.emit();
    }

    beginValueChange($event) {
        this.beginValue = $event;
    }

    endValueChange($event) {
        this.endValue = $event;
    }
}